<template>
  <div class="mainXiaoGuo">
    <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>{{$route.query.name}} </h1>
      <div class="chartWrap">
        <router-link :to="{path:'/search',query: {}}">
          <span class="search iconfont"> &#xe625; </span>
        </router-link>
        <span class="bag" v-if="indexData.shoppingCartNum > 0"> <em class="font10"> {{indexData.shoppingCartNum}} </em> </span>
        <a class="iconfont cart"  @click="$router.push('cart')"> &#xe669; </a></div>
    </section>
      <section class="salesWrap">
        <div class="box box-flex justify-between">
          <div class="timerWrap">
            <h3 class="homeTittle" > Best sales of this Category </h3>
          </div>
          <div>
            <a href="javascript:void(0)" @click="$router.push({path:'searchResult',query:{category1Code:categoryOneCode,orderByVolume:'DESC'}})" class="blue"> See All</a>
          </div>
        </div>
        <div class="commonPro">
          <swiper :options="commonSwiperOption">
            <template v-for="(item,index) in categoryTwo.cateSpuVo ">
              <swiper-slide>
                <div class="item">
                  <div class="tips_cutOff" v-if="item.percentage !='0%'"> {{item.percentage}} OFF </div>
                  <div class="tips_love iconfont"   :class="{ active: wishCode.indexOf(item.spuCode) > -1 }"
                    @click.stop="handlerLove(item)"> {{wishCode.indexOf(item.spuCode) > -1?"&#xe653;":"&#xe652;"}} </div>
                  <div class="tips_sale" v-if="item.sale"> </div>
                  <img class="img" :src="item.spuMainImage" alt="">
                </div>
                <div class="name"> {{item.name}} </div>
                <div class="bottomPrice">
                  <p class="red"> {{ getStr(item.priceRange) }} AED  </p>
                  <p class="gray"> {{ getStr(item.priceRangeLine) }} AED </p>
                  <p v-if="item.isTypeFour==1"> Free Shipping </p>
                </div>
                <div class="downShare">
                  <div :class="{'active':item.share}"> <span class="share iconfont"> &#xe650; </span> </div>
                  <div :class="{'active':item.cart}"  @click.stop="addChart(item)"> <span class="cart iconfont"> &#xe669; </span> </div>
                </div>
              </swiper-slide>
            </template>
          </swiper>
        </div>

        <div class="box mt10 box-flex justify-between">
          <div class="timerWrap">
            <h3 class="homeTittle" > {{$route.query.name}}  </h3>
          </div>
          <div>
            <a href="javascript:void(0)" class="blue" @click="$router.push({path:'searchResult',query:{category1Code:categoryOneCode}})"> See All</a>
          </div>
        </div>
        <div class="categrorySales">
            <ul class="clearfix">
              <li v-for="(item,index) in categoryTwo.cateTwoVo" :key="index" @click="$router.push({path:'searchResult',query:{category2Code:item.categoryCode}})">
                <div class="item">
                  <img class="img" :src="item.img" alt="">
                  <div class="bottomName">
                    {{item.categoryName}}
                  </div>
                </div>
              </li>
            </ul>
        </div>

      </section>

        <!-- 添加购物车 -->
    <add-cart ref="addCart"> </add-cart>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';
export default {
  name: 'home',
  data () {
    return {
      show:false,
      categoryOneCode:this.$route.query.categoryCode,
      commonSwiperOption:{
        slidesPerView: 2.3,
        spaceBetween: 10,
        freeMode: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
      },
      addChartVisible:false,
      timer:{
        "d":"00",
        "h":"00",
        "m":"00",
        "s":"00"
      },
    }
  },
  computed: {
    ...mapGetters(['categoryTwo','indexData',  "wishCode",
      "wishes"])
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){
        this.$store.dispatch('getCategoryTwo',{parentCategoryCode:this.categoryOneCode}).then(res=>{})
    },
    goBack(){
      this.$router.back(-1)
    },
    // 弹出规格
    addChart(data) {
      this.$refs.addCart.load(data);
    },
    handlerLove(item) {
      let index = this.wishCode.indexOf(item.spuCode);
      let isAdd = index == -1;
      this.$store
        .dispatch(
          isAdd ? "addWish" : "deleteWish",
          isAdd
            ? { type: "spu", code: item.spuCode }
            : { id: this.wishes.result[index].id }
        )
        .then(res => {
          if(isAdd){
            this.$toast("Move to Whislist Successfully");
          }else{
            this.$toast("Remove From Whislist Successfully");
          }
          this.$store.dispatch("myWishes");
        });
  },
  getStr(obj){
      var index = obj.indexOf("-");
      if(index != -1){
        var str = obj.substring(0,index-1);
        return str;
      }else{
        return obj;
      }
    }

  }
}
</script>

<style lang="scss" scoped>
  @import "../../common/css/category.scss";
</style>



